@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;600&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Source Sans Pro', sans-serif;
}

#root {
    height: 100vh;
    width: 100vw;
    background-color: darkgray;
    overflow: auto;
}

#menu-content-wrapper {
    display: flex;

}
#header {
    color: white;
    font-size: 50px;
    background: rgb(147,147,147);
    background: linear-gradient(90deg, rgba(147,147,147,1) 0%, rgba(0,0,0,1) 99%);
    text-align: center;
    height: 1.5em;
}

#company-name-header {
    background-color: #266e6e;
    text-align: center;
    color: white;
}

#menu-wrapper {
    background-color: black;
    color: white;
    flex: 1 1;
    height: 100%;
    overflow: hidden;
    min-width: 300px;
}

.menu-item {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 1em;
}

.menu-item:hover {
    background-color: darkcyan;
    transition: .5s;
}

.menu-item-selected {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 1em;
    background-color: #225d5d;
    transition: .5s;
}
#content {
}

input {
    margin: 2px;
    padding: 1em;
}

#tech-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.doc-wrapper {
    display: flex;
    flex-direction: column;
    background-color: white;
    /*width: 100%;*/
}

.doc-link {
    margin: 4px;
    padding: 1em;
    background-color: #357c7c;
    text-align: center;
    color: white;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.doc-link:hover {
    background-color: #225d5d;
    transition: .5s;
}

.content-wrapper {
    flex: 4 1;
    background-color: lightgray;
}
h3 {
    padding: 1em;
    background-color: #303432;
    color: white;
}

p {
    padding: 1em;
}

button {
    width: 100px;
    height: 50px;
    margin: 1em;
}

.template-wrapper {
    background-color: white;
}

.content-wrapper#cover-letter-generator{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.template-sub-wrapper {
    flex: 1 1;
}

#cover-letter-content {
    flex: 4 1;
    font-size: 12pt;
}

#letter-text {
    padding: 1em;
}

.template-item {
    background-color: #666767;
    color: white;
    padding: 1em;
}

.template-item-selected {
    background-color: #225d5d;
    color: white;
    padding: 1em;

}
#letter-text {
    font-family: 'Source Sans Pro', sans-serif;
    padding: 5em;
}


a {
    padding-left: 4px;
    padding-right: 4px;
    color: blue;
}

a:visited {
    color: blue;
}

#line-separator {
    height: 1px;
    width: 100%;
    background-color: black;
}

#job-title-header {

    color: #18518d;
}
#links-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.social-link {
    margin: 4px;
    flex: 1 1;
    background-color: #666767;
    text-align: center;
    color: white;
    padding-top: 1em;
    padding-bottom: 1em;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.social-link:hover {
    background-color: #266e6e;
    transition: .5s;
}

.social-link-selected {
    margin: 4px;
    flex: 1 1;
    background-color: #266e6e;
    transition: .5s;
    padding-top: 1em;
    padding-bottom: 1em;
    color: white;
    text-align: center;
}

.job {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 200px;
    height: 100px;
}

.job:hover {
    background-color: #225d5d;
    color: white;
}

#job-selected {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 200px;
    height: 100px;
    background-color: #225d5d;
}
#app-item-wrapper {
    display: flex;
    flex-direction: column;
}


.app-item {
    background-color: white;
    margin: 4px;
    flex: 1 1;
    text-align: center;
    padding-top: 1em;
    padding-bottom: 1em;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.delete {
    background-color: red;
    margin: 4px;
    flex: 1 1;
    text-align: center;
    padding-top: 1em;
    padding-bottom: 1em;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;

}
.delete:hover {
    background-color: darkred;
    transition: .5s;
}
.content-wrapper#application {
    display: flex;
}

#application-list-wrapper {
    flex: 1 1;
    overflow-y: auto;
    height: 90vh;
}

#application-form-wrapper {
    flex: 2 1;
}

#application-search-form input {
    text-align: center;
    outline: none;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

#application-search-form button {
    outline: none;
    margin: 0;
    width: 100%;
}

#application-search-form button:hover {
    background-color: #266e6e;
    transition: .5s;
    color: white;
}
